









import Loading from "@/views/Loading.vue";
import Vue from "vue";
import { cuentaClient } from "@/services/api/api.service";
export default Vue.extend({
  components: { Loading },
  data() {
    return {
      offlineConfirmed: false
    };
  },
  created(): void {
    cuentaClient
      .ping()
      .then(() => {
        window.location.reload();
      })
      .catch(() => {
        this.offlineConfirmed = true;
      });
  }
});
